<template>
    <div id="zhuan">
      <div class="box" v-show="iftype == 1">
        <div class="flex">
          <img src="../../assets/sy.png" alt="">
          <h3></h3>
        </div>
        <p><span></span><span></span></p>
      </div>
      <div class="box" v-show="iftype == 2">
        <div class="flex">
          <img src="../../assets/mz.png" alt="">
          <h3></h3>
        </div>
        <p><span></span><span></span><span></span><span></span></p>
      </div>
      <div class="code" v-show="ifshowCode">
        <p v-show="iftype == 1"><span></span></p>
        <p v-show="iftype == 2"><span></span></p>
        <img :src="imgPath" alt="">
      </div>
      <div class="choose flex" @click="choose" v-show="!ifshowCode">
        <p v-if="value == ''"></p>
        <p v-else>{{value}}</p>
        <img src="../../assets/hei.png" alt="">
      </div>
      <van-popup v-model="showPicker" position="bottom">
        <van-picker
          show-toolbar
          :columns="columns"
          @cancel="showPicker = false"
          @confirm="onConfirm"
        />
      </van-popup>
      <p class="month" v-if="!ifshowCode && value == '' && !ifknow"></p>
      <div v-show="showdata">
        <div class="choose flex" @click.stop="show = true" v-show="!ifshowCode" :class="{choosee: !ifknow}">
          <img src="../../assets/ri.png" alt="">
          <input type="text" placeholder="" :value="tim" class="flexs" v-if="value == '' && ifknow" readonly>
          <input type="text" :placeholder="formatDate(currentDate)" :value="tim" class="flexs" v-if="value == '' && !ifknow" readonly>
          <input type="text" placeholder="" :value="tim" class="flexs" v-if="value == ''" readonly>
          <img src="../../assets/hei.png" alt="">
        </div>
        <van-popup v-model="show" position="bottom" :style="{ height: '40%' }">
          <van-datetime-picker
            v-model="currentDate"
            type="date"
            @confirm="queren"
            @cancel="show = false"
          />
        </van-popup>
      </div>
      <div class="help" v-show="!ifshowCode && value == '' && ifknow" @click="ifknow = false,ifanswer = false"></div>
      <div class="help" v-show="!ifshowCode && value == '' && !ifknow" @click="ifknow = true"></div>
      <div class="answer" v-show="!ifshowCode && !ifknow && ifanswer"><span>{{suan}}</span></div>
      <div class="btn" @click="search" v-show="!ifshowCode && ifknow || ifanswer"></div>
      <div class="btn" v-show="!ifshowCode && !ifknow && !ifanswer" @click="helpme"></div>
      <div class="shili">
        <div class="head flex">
          <div></div>
          <p class="flexs"></p>
        </div>
        <ul class="flex">
          <li class="flex" v-show="iftype == 1"><img src="../../assets/s1.png" alt=""></li>
          <li class="flex" v-show="iftype == 1"><img src="../../assets/s2.png" alt=""></li>
          <li class="flex" v-show="iftype == 2"><img src="../../assets/m1.png" alt=""></li>
          <li class="flex" v-show="iftype == 2"><img src="../../assets/m2.png" alt=""></li>
          <li class="flex" v-show="iftype == 2"><img src="../../assets/m3.png" alt=""></li>
          <li class="flex" v-show="iftype == 2"><img src="../../assets/m4.png" alt=""></li>
        </ul>
      </div>
    </div>
</template>

<script>
    import { addboluojie } from '../../server/getData'
    export default {
      name: "index",
      data(){
        return{
          imgPath:'',// 
          suan:'',// 
          showdata: false, // 
          currentDate: new Date(),
          show: false,
          tim: '',
          ifknow: true, // true  / flase 
          ifanswer: false, // 
          ifshowCode: false, // 
          iftype: 1, // 1. 2.
          value: '',
          showPicker: false,
          // valindex: 0, // 
          columns: ['', '', '']
        }
      },
      methods:{
        formatDate(date) {
          var year = date.getFullYear();
          var month = date.getMonth() + 1;
          var day = date.getDate();
          return year + "-" + this.formatTen(month) + "-" + this.formatTen(day);
        },
        formatTen(num) {
          return num > 9 ? (num + "") : ("0" + num);
        },
        queren(value){
          this.tim = this.formatDate(value)
          this.show = false
        },
        // 
        search(){
          console.log(this.value, this.tim);
          if(this.value == '') return
          if(this.value != '' && this.tim == '') return
          this.ifshowCode = true
          this.ifknow = false
          this.ifanswer = false
          let parmars = {
            act: 'getQrCode',
            edc: this.tim || '1111-11-11'
          }
          addboluojie(parmars).then(res => {
            this.imgPath = res.imgPath
          }).catch(err => {
            console.log(err);
          });
        },
        // 
        helpme(){
          this.ifanswer = true
          let parmars = {
            act: 'setedc',
            day: this.tim
          }
          addboluojie(parmars).then(res => {
            this.suan = res.edc
          }).catch(err => {
            console.log(err);
          });
        },
        // 
        onConfirm(value) {
          this.value = value;
          this.showPicker = false;
          if(this.value != ''){
            this.showdata = true
            if(this.value == ''){
              this.ifknow = true
            }
          } else {
            this.showdata = false
            this.ifknow = true
          }
        },
        // 
        choose(){
          // if(this.value != ''){
          //   for(let i = 0; i <this.columns.length; i++){
          //     if(this.columns[i] == this.value){
          //       this.valindex = i
          //     }
          //   }
          // }
          this.showPicker = true
        }
      },
      created() {
        if(this.$route.query.iftype){
          this.iftype = this.$route.query.iftype
          if(this.$route.query.iftype == 1){
            document.title = ''
          } else {
            document.title = ''
          }
        } else {
          document.title = ''
        }

        let parmars = {
          act: 'getuserdata'
        }
        addboluojie(parmars).then(res => {
          if(res.status != 0) {
            this.ifshowCode = true
            this.imgPath = res.imgPath
          }
        }).catch(err => {
          console.log(err);
        });
      }
    }
</script>
<style>
  body{
    background: #FAFAFA;
  }
</style>
<style scoped lang="scss">
 #zhuan{
   overflow: hidden;
   .box{
     background: white;
     box-shadow:0px 8px 29px 0px rgba(236,236,236,0.5);
     border-radius:0 0 48px 48px;
     overflow: hidden;
     div{
       justify-content: center;
       margin: 40px auto;
       img{
         width: 27px;
         margin-right: 15px;
         margin-top: 10px;
       }
       h3{
         font-size: 36px;
         color: #252525;
       }
     }
     p{
       font-weight: bold;
       color: #252525;
       font-size: 28px;
       padding: 0 50px 70px;
       line-height: 46px;
       span{
         color: #FF7742;
         font-size: 30px;
       }
     }
   }
   .code{
     margin: 65px 0 90px;
     img{
       width: 264px;
       margin: 0 auto;
     }
     p{
       color: #565656;
       font-size: 28px;
       text-align: center;
       margin-bottom: 46px;
       span{
         color: #FF7742;
       }
     }
   }
   .choose{
     margin: 70px 50px 0;
     border-bottom: 2px solid #E5E5E5;
     p{
       font-weight: bold;
       font-size: 32px;
       color: #252525;
       padding: 24px 0;
     }
     img{
       width: 36px;
       margin-right: 18px;
     }
     input:nth-child(2){
       color: #252525;
       font-size: 28px;
       font-weight: normal;
       background: none;
       outline: none;
       border: none;
       padding: 24px 0;
     }
     img:last-child{
       width: 25px;
     }
   }
   .month{
     font-size: 30px;
     color: #252525;
     padding-left: 50px;
     margin-top: 100px;
   }
   .choosee{
     margin-top: 20px;
   }
   .help{
     font-size: 28px;
     text-align: right;
     margin-right: 40px;
     color: #FF7E52;
     margin-top: 50px;
   }
   .answer{
     font-size: 30px;
     color: #3F3E3E;
     margin: 50px 0 0 50px;
     span{
       font-weight: bold;
       color: #FF7139;
       font-size: 36px;
     }
   }
   .btn{
     width:570px;
     height:88px;
     line-height:88px;
     background:linear-gradient(25deg,#FF8A62,#FDBA60);
     box-shadow:0px 10px 46px 0px rgba(255,141,86,0.41);
     border-radius:44px;
     font-size: 32px;
     font-weight: bold;
     color: white;
     text-align: center;
     margin: 80px auto;
   }
   .shili{
     padding: 0 50px 60px;
     .head{
       margin-bottom: 14px;
       div{
         width:7px;
         height:27px;
         background: #FF8454;
       }
       p{
         font-weight: bold;
         color: #252525;
         font-size: 32px;
         margin-left: 10px;
       }
     }
     ul{
       flex-wrap: wrap;
       li{
         img{
           width: 300px;
           margin-top: 26px;
         }
       }
     }
   }
 }
</style>



