<template>
    <div id="zhuan">
      <div class="box">
        <h3 v-show="type == 1"></h3>
        <h3 v-show="type == 2"></h3>
        <p v-show="type == 2">...</p>
        <ul class="flex">
          <li class="flex" :class="{active:titleIndex == 1}">
            <img src="../../assets/a.png" alt="">
            <p></p>
            <div></div>
          </li>
          <li class="flex" :class="{active:titleIndex == 2}">
            <img src="../../assets/b1.png" alt="" v-if="titleIndex == 2">
            <img src="../../assets/b0.png" alt="" v-else>
            <p></p>
            <div></div>
          </li>
          <li class="flex" :class="{active:titleIndex == 3}">
            <img src="../../assets/c1.png" alt="" v-if="titleIndex == 3">
            <img src="../../assets/c0.png" alt="" v-else>
            <p></p>
            <div></div>
          </li>
        </ul>
      </div>
      <div v-show="titleIndex == 2" class="txt">
        <p v-if="ifknow"></p>
        <p v-else></p>
        <p v-if="type == 1"></p>
        <p v-else></p>
      </div>
      <div v-show="titleIndex == 3" class="txt">
        <p></p>
        <p v-if="type == 1"></p>
        <p v-else></p>
      </div>
      <van-popup v-model="show" position="bottom" :style="{ height: '40%' }">
        <van-datetime-picker
          v-model="currentDate"
          type="date"
          @confirm="queren"
          @cancel="show = false"
        />
      </van-popup>
      <div class="date flex" @click="showPopup">
        <img src="../../assets/ri.png" alt="">
        <p class="flexs">{{tim}}</p>
        <img src="../../assets/yellow.png" alt="">
      </div>
      <div class="help" v-show="titleIndex == 2 && ifknow" @click="ifknow = false,ifanswer = false"></div>
      <div class="help" v-show="titleIndex == 2 && !ifknow" @click="ifknow = true"></div>
      <div class="answer" v-show="!ifknow && ifanswer"><span>{{suan}}</span></div>
      <div class="btn" v-if="ifknow || ifanswer" @click="add"></div>
      <div class="btn" v-else @click="helpme"></div>
    </div>
</template>

<script>
    import { addboluojie } from '../../server/getData'
    export default {
      name: "index",
      data(){
        return{
          code: '', // 
          suan:'',// 
          ifknow: true, // true  / flase 
          ifanswer: false, // 
          type:1, // 1. 2.
          currentDate: new Date(),
          show: false,
          titleIndex: 1,
          tim: this.formatDate(new Date())
        }
      },
      methods:{
        formatDate(date) {
          var year = date.getFullYear();
          var month = date.getMonth() + 1;
          var day = date.getDate();
          return year + "-" + this.formatTen(month) + "-" + this.formatTen(day);
        },
        formatTen(num) {
          return num > 9 ? (num + "") : ("0" + num);
        },
        queren(value){
          this.tim = this.formatDate(value)
          this.show = false
        },
        showPopup() {
          this.show = true;
        },
        // choosewhich(index) {
        //   if(index == 1) {
        //     this.$router.push({path:'/code',query:{type: this.type,ifbei: 1}})
        //   } else {
        //     if (index === this.titleIndex) {
        //       return;
        //     }
        //     this.titleIndex = index;
        //   }
        // },
        // 
        helpme(){
          this.ifanswer = true
          let parmars = {
            act: 'setedc',
            day: this.tim
          }
          addboluojie(parmars).then(res => {
            this.suan = res.edc
          }).catch(err => {
            console.log(err);
          });
        },
        // 
        add(){
          let parmars = {
            act: 'getQrCode',
            edc: this.tim
          }
          if(!this.ifknow && this.ifanswer){
            parmars.edc = this.suan
          }
          addboluojie(parmars).then(res => {
            if(this.$route.query.url){
              window.location.href = this.$route.query.url
            } else {
              this.$router.push({path:'/code',query:{type: this.type,ifbei: 2,imgPath: res.imgPath,code: this.code}})
            }
          }).catch(err => {
            console.log(err);
          });
        }
      },
      created() {
        if(this.$route.query.type){
          this.type = this.$route.query.type
          this.code = this.$route.query.code
          this.titleIndex = this.$route.query.titleIndex
          if(this.$route.query.type == 1){
            document.title = ''
          } else {
            document.title = ''
          }
        }
      }
    }
</script>
<style>
  body{
    background: #FAFAFA;
  }
</style>
<style scoped lang="scss">
 #zhuan{
   overflow: hidden;
   .box{
     background: white;
     border-radius: 0 0 48px 48px;
     box-shadow:0px 14px 29px 0px rgba(236,236,236,0.46);
     overflow: hidden;
     h3{
       font-size: 36px;
       color: #252525;
       text-align: center;
       margin: 40px auto;
     }
     >p{
       color: #999999;
       font-size: 24px;
       padding-bottom: 60px;
       text-align: center;
     }
     ul{
       padding: 0 40px 60px;
       li{
         width:182px;
         height:140px;
         background: white;
         box-shadow:0px 8px 29px 0px rgba(236,236,236,0.65);
         border-radius:16px;
         align-items: center;
         flex-direction: column;
         justify-content: space-around;
         position: relative;
         img{
           width: 60px;
         }
         p{
           font-size: 28px;
           color: #9A948E;
         }
         div{
           position: absolute;
           width: 30px;
           height: 30px;
           background: #FF8454;
           transform: rotate(45deg);
           bottom: -76px;
           display: none;
         }
         &.active{
           background: url("../../assets/back.png") no-repeat;
           background-size: 100% 100%;
           box-shadow:0px 10px 46px 0px rgba(255,141,86,0.41);
           p{
             font-size: 30px;
             color: white;
           }
           div{
             display: block;
           }
         }
         &:nth-child(2){
           img{
             width: 40px;
           }
         }
       }
     }
   }
   .txt{
     margin: 84px 0 100px;
     p:first-child{
       font-weight: bold;
       color: #252525;
       font-size: 34px;
       padding-left: 30px;
     }
     p:last-child{
       color: #999999;
       font-size: 26px;
       margin-top: 46px;
       padding: 0 40px;
     }
   }
   .bei{
     img{
       width: 267px;
       margin: 110px auto 65px;
     }
     p{
       color: #565656;
       font-size: 32px;
       text-align: center;
     }
   }
   .date{
     margin: 0 30px 0 50px;
     border-bottom: 2px solid #EEEEEE;
     padding: 18px 0;
     img:first-child{
       width: 38px;
     }
     p{
       font-weight: bold;
       font-size: 34px;
       color: #252525;
       margin-left: 18px;
     }
     img:last-child{
       width: 25px;
     }
   }
   .help{
     font-size: 28px;
     text-align: right;
     margin-right: 40px;
     color: #FF7E52;
     margin-top: 50px;
   }
   .answer{
     font-size: 30px;
     color: #3F3E3E;
     margin: 50px 0 0 50px;
     span{
       font-weight: bold;
       color: #FF7139;
       font-size: 36px;
     }
   }
   .btn{
     position: fixed;
     bottom: 140px;
     left: 90px;
     width:570px;
     height:88px;
     line-height:88px;
     background:linear-gradient(25deg,#FF8A62,#FDBA60);
     box-shadow:0px 10px 46px 0px rgba(255,141,86,0.41);
     border-radius:44px;
     color: white;
     font-size: 36px;
     font-weight: bold;
     text-align: center;
   }
 }
</style>



