import Vue from 'vue'
import Router from 'vue-router'
import choose from '@/pages/choose/index' // /
import code from '@/pages/code/index' // 
import nobeiyun from '@/pages/nobeiyun/index' // /
import shengyu from '@/pages/shengyu/index' // /

Vue.use(Router)

const routes = [
  {path: '/', name: 'choose', component: choose},
  {path: '/code', name: 'code', component: code},
  {path: '/nobeiyun', name: 'nobeiyun', component: nobeiyun},
  {path: '/shengyu', name: 'shengyu', component: shengyu},
]


const router = new Router({
  routes
})

router.beforeEach((to, from, next) => {
  document.title = to.meta.title;
  next();
});
router.afterEach((to, from, next) => {
  window.scrollTo(0, 0)
})

export default router



