import axios from 'axios'
import qs from 'qs'

let baseurl = ''
if(window.location.host.indexOf('localhost') != -1){
  baseurl = '/api'
} else{
  baseurl = window.location.protocol+"//"+ window.location.host
}

export default {
  wxShowMenu: function(data) {
    let parmars = {
      act: 'getJsSignature',
      url: encodeURIComponent(window.location.href)
    };
    axios.post(
      baseurl + '/activity/mobileapi/do.php',
      qs.stringify(parmars)
    ).then(function(res) {
      var getMsg = res.data.signature
      wx.config({
        debug: false, //debug
        appId: getMsg.appId, //appId
        timestamp: getMsg.timestamp, //
        nonceStr: getMsg.nonceStr, //
        signature: getMsg.signature, //
        jsApiList: [ //JS
          'onMenuShareTimeline', //
          'onMenuShareAppMessage', //
          'getLocation'
        ]
      });
      wx.ready(function() {
        // 
        wx.onMenuShareTimeline({
          title: data.titles, // 
          desc: data.descs, //
          link: data.link, // 
          imgUrl: 'https://wx.boluoyunyu.com/bjchs/img/logo.jpg' // 
        });
        //
        wx.onMenuShareAppMessage({
          title: data.titles, // 
          desc: data.descs, //
          link: data.link, // 
          imgUrl: 'https://wx.boluoyunyu.com/bjchs/img/logo.jpg' // 
        });
      });
    })
  }

}



