  <div id="zhuan">
    <div class="box">
      <h3 v-if="code" class="codetitle"></h3>
      <h3 v-else></h3>
      <p v-show="type == 1 && ifbei && !code">:</p>
      <p v-show="type == 2 && ifbei">:</p>
      <p v-show="type == 1 && !ifbei && !code" style="text-align: center">:</p>
      <p v-show="type == 2 && !ifbei" style="text-align: center">:</p>
      <h4 v-show="type == 1 && code"></h4>
      <h5 v-show="type == 1 && code">{{code}}</h5>
    </div>
    <div class="bei">
      <img :src="imgPath" alt="" :class="code?'anhao':''">
      <p v-if="miniprogram == 1"></p>
      <p v-if="miniprogram == 1" class="after"></p>
      <img v-if="miniprogram == 1" src="../../assets/sao.png" alt="" class="mini">
      <div v-else>
        <p class="flexs" v-if="code"></p>
        <p class="flexs" v-else></p>
      </div>
    </div>

<!--    <div class="maybe" v-show="type == 1">-->
