<!--      <ol class="flex">-->
<!--        <li><img src="../../assets/quns/1-1.png" alt="" @click="addcode(0)"></li>-->
<!--        <li><img src="../../assets/quns/1-2.png" alt="" @click="addcode(0)"></li>-->
<!--        <li><img src="../../assets/quns/1-3.png" alt="" @click="addcode(2)"></li>-->
<!--        <li><img src="../../assets/quns/1-4.png" alt="" @click="addcode(1)"></li>-->
<!--      </ol>-->
<!--      <ol class="flex">-->
<!--        <li><img src="../../assets/quns/2-1.png" alt="" @click="addcode(1)"></li>-->
