<!--        <li><img src="../../assets/quns/2-1.png" alt="" @click="addcode(1)"></li>-->
<!--        <li><img src="../../assets/quns/3-2.png" alt="" @click="addcode(1)"></li>-->
<!--        <li><img src="../../assets/quns/3-3.png" alt="" @click="addcode(1)"></li>-->
<!--        <li><img src="../../assets/quns/3-4.png" alt="" @click="addcode(1)"></li>-->
<!--      </ol>-->
<!--    </div>-->

    <div class="huai flex" v-show="ifbei" @click="tonobeiyun">
      <p></p>
      <img src="../../assets/jiao.png" alt="">
    </div>
  </div>
</template>

<script>
  import {addboluojie} from '../../server/getData'
  import addcode from '../../components/addcode'

  export default {
    name: "index",
    data() {
      return {
        codenum: 1, // 
        ifshowCode:false,
        maybedata: [
          {
            'h1': '',
            'p': '<em style="color: #F5632E;text-decoration: underline;"></em>',
            'arr': ['', '', '']
          },
          {
            'h1': '',
            'p': '<em style="color: #F5632E;text-decoration: underline;"></em>',
            'arr': ['(', '', '']
          },
          {
            'h1': '',
            'p': '<em style="color: #F5632E;text-decoration: underline;"></em>',
            'arr': ['', '']
          },
          {
            'h1': '',
            'p': '<em style="color: #F5632E;text-decoration: underline;"></em>',
            'arr': ['', '', '']
          }
        ],
        miniprogram: localStorage.getItem('miniprogram'),
        code: '', // 
        imgPath: '',
        type: 1, // 1. 2.
        ifbei: true // 
      }
    },
    methods: {
      // 
      addcode(index){
        this.ifshowCode = true
        this.codenum = index + 1
      },
      tonobeiyun() {
        this.$router.push({path: '/nobeiyun', query: {type: this.type, titleIndex: 2}})
      },
    },
    created() {
      if (this.$route.query.type) {
        this.type = this.$route.query.type
        if (this.$route.query.type == 1) {
          document.title = ''
          if (this.$route.query.code) {
            this.code = this.$route.query.code
          }
        } else {
          document.title = ''
        }
      }
      if (this.$route.query.ifbei) {
        if (this.$route.query.ifbei == 1) {
          this.ifbei = true
        } else {
          this.ifbei = false
        }
      }
      if (this.$route.query.imgPath) {
        this.imgPath = this.$route.query.imgPath
      } else {
        let parmars = {
          act: 'getQrCode',
          edc: '1111-11-11'
        }
        addboluojie(parmars).then(res => {
          this.imgPath = res.imgPath
        }).catch(err => {
          console.log(err);
        });
      }
    },
    mounted() {
      let that = this
      window.addEventListener('popstate', function () {
        if (window.location.href.indexOf('which')) {
          if (sessionStorage.getItem('first')) {
            console.log(11)
            sessionStorage.removeItem('first')
            that.$router.go(-3);
          } else {
            console.log(22)
            that.$router.go(-1);
          }
        } else {
          if (sessionStorage.getItem('first')) {
            console.log(33)
            sessionStorage.removeItem('first')
            that.$router.go(-4);
          } else {
            console.log(44)
            that.$router.go(-2);
          }
        }
      }, false)
    },
    components: {
      addcode
    },
    destroyed() {
      let that = this
      window.removeEventListener('popstate', function () {
        if (that.$route.query.which) {
          if (sessionStorage.getItem('first')) {
            that.$router.go(-3);
          } else {
            that.$router.go(-2);
          }
        } else {
          if (sessionStorage.getItem('first')) {
            that.$router.go(-4);
          } else {
            that.$router.go(-3);
          }
        }
      }, false);//false
    },
  }
</script>
<style>
  body {
    background: #FAFAFA;
  }
</style>
<style scoped lang="scss">
  #zhuan {
    overflow: hidden;

    .box {
      h3 {
        font-size: 36px;
        color: #252525;
        text-align: center;
        margin: 40px auto;

