        text-align: center;
        font-weight: normal;
      }

      h5 {
        color: #FF5509;
        font-size: 34px;
        text-align: center;
        margin: 20px 0 50px;
      }

      ul {
        padding: 0 40px 60px;

        li {
          width: 182px;
          height: 140px;
          background: white;
          box-shadow: 0px 8px 29px 0px rgba(236, 236, 236, 0.65);
          border-radius: 16px;
          align-items: center;
          flex-direction: column;
          justify-content: space-around;
          position: relative;

          img {
            width: 60px;
          }

          p {
            font-size: 28px;
            color: #9A948E;
          }

          div {
            position: absolute;
            width: 30px;
