            transform: rotate(45deg);
            bottom: -76px;
            display: none;
          }

          &.active {
            background: linear-gradient(14deg, #FF8A62, #FDBA60);
            box-shadow: 0px 10px 46px 0px rgba(255, 141, 86, 0.41);

            p {
              font-size: 30px;
