            }

            div {
              display: block;
            }
          }
        }
      }
    }

    > p {
      margin: 100px 0 60px;
      font-weight: bold;
      color: #252525;
      font-size: 34px;
      padding-left: 30px;
    }

    .bei {
      img {
        width: 366px;
        margin: 0 auto 40px;

        &.anhao {
          border: 2px solid #FFBD89;
        }
      }

      p {
        color: #565656;
        font-size: 28px;
        text-align: center;
      }
    }

    .maybe {
      border-top: 26px solid #F0F0F0;
      margin-top: 50px;
      padding: 0 30px;
      margin-bottom: 100px;

      h3 {
        font-size: 30px;
