        margin: 25px 8px 25px 0;
      }

      img {
        width: 45px;
        margin: 25px 0;
      }
    }

    .after {
      font-size: 30px !important;
      margin: 60px 30px 40px 30px;
      color: #252525 !important;
      font-weight: bold;
    }

    .mini {
      width: 65% !important;
    }
  }
</style>



