<template>
  <div id="app">
    <router-view v-show="!hei"/>
    <p v-show="hei">...</p>
  </div>
</template>

<script>
import { iflogo,ifhei } from './server/getData'
export default {
  name: 'App',
  data(){
    return {
      hei: false,
    }
  },
  created(){
      let parmars1 = {
        act: 'getsessionstatus',
      }
      let parmars2 = {
        act: 'isBlack',
      }
      iflogo(parmars1).then(res => {
        if(res.status == 0) {
          sessionStorage.setItem('first',true)
          var str = window.location.href
          if(str.indexOf('boluoyunyu') != -1){
            window.location.href= window.location.protocol + "//" + window.location.host + "/activity/mobileapi/addboluojie/getuserinfo.php?hid=" + str.substr(str.indexOf('#/') + 2);
          }
        }
      })
      ifhei(parmars2).then(res => {
      if(res.isBlack == 1) {
        this.hei = true
      }
    })
  },
}
</script>

<style>
  *{
    margin: 0;
    padding: 0;
    font-family: PingFangSC-Regular, sans-serif;
    box-sizing: border-box;
  }
  ol,li,ul{
    list-style: none;
  }
  a {
    text-decoration: none;
  }
  input,
  button,
  select,
  textarea {
    outline: none;
  }
  img{
    display: block;
    width: 100%;
  }
  .flex{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .flexs{
    flex: 1;
  }
  .sheng{
    overflow: hidden;
    text-overflow:ellipsis;
    white-space: nowrap;
  }



  .swiper-pagination-bullet{
    width: 12px;
    height: 12px;
    background: #D8D8D8;
    opacity: 1;
    border-radius: 50%;
  }
  .swiper-pagination-bullet-active{
    background: #FF7011;
  }


  .van-loading__text{
    font-size: 28px;
  }
  .van-picker__toolbar{
    height: 80px !important;
  }
  .van-picker__cancel, .van-picker__confirm,.van-picker-column{
    font-size: 30px !important;
    color: #FF7E52 !important;
  }
  .van-toast--html, .van-toast--text{
    width: 300px;
    height: 100px;
    font-size: 32px;
    border-radius: 20px;
  }
</style>



