<template>
  <div class="back" @click="closeCodeHandle">
    <div>
      <div class="head flex">
        <p v-show="isIndex == 1"></p>
        <p v-show="isIndex == 2"></p>
        <p v-show="isIndex == 3"></p>
        <p v-show="isIndex == 4"></p>
        <img src="../assets/mai.png" alt="" v-if="isIndex == 2">
        <img src="../assets/nomai.png" alt="" v-else>
        <img src="../assets/closeadd.png" alt="" class="close">
      </div>
      <img v-show="isIndex == 1" src="../assets/yuezi.png" alt="">
      <img v-show="isIndex == 2" src="../assets/mai.jpg" alt="">
      <img v-show="isIndex == 3" src="../assets/zhuanjia.png" alt="">
      <img v-show="isIndex == 4" src="../assets/yuersao.jpg" alt="">
      <p v-show="isIndex == 1"></p>
      <p v-show="isIndex == 2"></p>
      <p v-show="isIndex == 3"> </p>
      <p v-show="isIndex == 4"> </p>
    </div>
  </div>
</template>

<script>
  export default {
    name: "addcode",
    props:{
      codeIndex:{
        type:Number,
        default: 1
      }
    },
    computed: {
      isIndex(){
        console.log(this.codeIndex)
        if(this.codeIndex==1){
          return 1
        }else if(this.codeIndex==2){
          return 2
        }else if(this.codeIndex==3){
          return 3
        }else if(this.codeIndex==4){
          return 4
        }
      }
    },

    methods: {
      closeCodeHandle() {
        this.$emit('closeCode')
      }
    }
  }
</script>

<style scoped lang="scss">
  .back {
    position: fixed;
    top: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, .6);
    z-index: 3;

    > div {
      position: absolute;
      width: 566px;
      height: 566px;
      left: 92px;
      top: 20%;
      background: white;
      border-radius: 22px;

      .head {
        position: relative;
        justify-content: center;
        padding-top: 54px;

        p {
          font-size: 32px;
          color: #252525;
          font-weight: bold;
        }

        img {
          width: 84px;
          border-radius: 50%;

          &.close {
            width: 70px;
            position: absolute;
            right: 0;
            top: 0;
            border-radius: 0;
          }
        }
      }

      > img {
        width: 254px;
        height: 254px;
        border: 2px solid #FFB968;
        margin: 40px auto 30px;
      }

      > p {
        color: #4E4E4E;
        font-size: 30px;
        text-align: center;
      }
    }
  }
</style>



