<template>
    <div id="zhuan">
      <div class="box">
        <h3></h3>
        <p v-show="type == 1"></p>
        <p v-show="type == 2"></p>
      </div>
      <div class="bei" :class="{active:titleIndex == 1}" @click="choosewhich(1)">
        <img src="../../assets/choose/check0.png" alt="" v-if="titleIndex !=1">
        <img src="../../assets/choose/check1.png" alt="" v-else>
        <div v-show="titleIndex != 1"></div>
      </div>
      <div class="flex two">
        <div class="huai" :class="{active:titleIndex == 2}" @click="choosewhich(2)">
          <img src="../../assets/choose/check0.png" alt="" v-if="titleIndex !=2">
          <img src="../../assets/choose/check1.png" alt="" v-else>
          <div v-show="titleIndex != 2"></div>
        </div>
        <div class="sheng" :class="{active:titleIndex == 3}" @click="choosewhich(3)">
          <img src="../../assets/choose/check0.png" alt="" v-if="titleIndex !=3">
          <img src="../../assets/choose/check1.png" alt="" v-else>
          <div v-show="titleIndex != 3"></div>
        </div>
      </div>
      <div class="btn" @click="towhich"></div>
    </div>
</template>

<script>
    import { addboluojie } from '../../server/getData'
    export default {
      name: "index",
      data(){
        return{
          url: false,
          code: '', // 
          type:1, // 1. 2.
          titleIndex: 2 // 1. 2. 3.
        }
      },
      methods:{
        // 
        choosewhich(index) {
          if (index === this.titleIndex) {
            return;
          }
          this.titleIndex = index;
        },
        // 
        towhich(){
          if(this.titleIndex == 1){
            if(this.url){
              window.location.href = this.url
            } else {
              this.$router.push({path:'/code',query:{type: this.type,ifbei: 1,which:111,code: this.code}})
            }
          } else if(this.titleIndex == 2){
            this.$router.push({path:'/nobeiyun',query:{type: this.type,titleIndex: 2,which:111,code: this.code,url: this.url}})
          } else if (this.titleIndex == 3){
            this.$router.push({path:'/nobeiyun',query:{type: this.type,titleIndex: 3,which:111,code: this.code,url: this.url}})
          }
        }
      },
      created() {
        if(this.$route.query.type){
          this.type = this.$route.query.type
          if(this.$route.query.type == 1){
            document.title = ''
          } else {
            document.title = ''
          }
        } else {
          document.title = ''
        }

        // 
        if(this.$route.query.url){
          this.url = this.$route.query.url
        }

        let parmars = {
          act: 'getuserdata'
        }
        addboluojie(parmars).then(res => {
          if(this.$route.query.code == 1){ //  
            this.code = ''
          } else {
            this.code = res.code
          }
          if(res.status == 1) {  // 
            this.$router.push({path:'/code',query:{type: this.type,ifbei: 2,imgPath: res.imgPath,code: this.code,which:111}})
          } else if(res.status == 2){ // 
            this.$router.push({path:'/code',query:{type: this.type,ifbei: 1,code: this.code,which:111}})
          }
        }).catch(err => {
          console.log(err);
        });
      }
    }
</script>
<style>
  body{
    background: #FAFAFA;
  }
</style>
<style scoped lang="scss">
 #zhuan{
   overflow: hidden;
   .box{
     h3{
       font-size: 36px;
       color: #252525;
       text-align: center;
       margin: 40px auto;
     }
     >p{
       color: #999999;
       font-size: 26px;
       padding: 0 40px 60px;
     }
   }
   .bei,.huai,.sheng{
     position: relative;
     width:230px;
     height:170px;
     border-radius:16px;
     img{
       position: absolute;
       width: 38px;
       top: 17px;
       right: 17px;
     }
     div{
       position: absolute;
       width: 100%;
       height: 100%;
       z-index: 2;
       background: white;
       opacity: .3;
     }
   }
   .bei{
     margin: 100px auto 70px;
     background: url("../../assets/1.png") no-repeat;
     background-size: 100% 100%;
     &.active{
       box-shadow:0px 10px 46px 0px rgba(255,141,86,0.66);
     }
   }
   .two{
     justify-content: center;
     .huai{
       margin-right: 60px;
       background: url("../../assets/2.png") no-repeat;
       background-size: 100% 100%;
       &.active{
         box-shadow:0px 10px 46px 0px rgba(255,141,86,0.66);
       }
     }
     .sheng{
       background: url("../../assets/3.png") no-repeat;
       background-size: 100% 100%;
       &.active{
         box-shadow:0px 10px 46px 0px rgba(255,141,86,0.66);
       }
     }
   }
   .btn{
     width:570px;
     height:88px;
     line-height:88px;
     background:linear-gradient(25deg,#FF8A62,#FDBA60);
     box-shadow:0px 10px 46px 0px rgba(255,141,86,0.41);
     border-radius:44px;
     margin: 200px auto;
     color: white;
     font-size: 36px;
     font-weight: bold;
     text-align: center;
   }
 }
</style>



